<template>
     <div id="site">
          <div id="signup_container">
               <router-link id="home" to="/">
                    <img src="../assets/logo.svg" id="logo" />
               </router-link>
               <div id="signup_form">
                    <div id="signup_fields">
                         <div class="fields">
                              <label>Email:</label>
                              <div class="formfield">
                                   <img id="signup_email_icon" src="../assets/mail.svg" />
                                   <input
                                        id="signup_email"
                                        v-model="email"
                                        type="email"
                                        maxlength="80"
                                        :keyup.enter="signupRequest"
                                   />
                              </div>

                              <label>First Name:</label>
                              <div class="formfield">
                                   <img id="signup_email_icon" src="../assets/profile2.svg" />
                                   <input
                                        id="signup_firstName"
                                        v-model="firstName"
                                        type="text"
                                        pattern="[A-Za-z]{0,20}"
                                        :keyup.enter="signupRequest"
                                   />
                              </div>

                              <label>Last Name:</label>
                              <div class="formfield">
                                   <img id="signup_email_icon" src="../assets/profile2.svg" />
                                   <input
                                        id="signup_lastName"
                                        v-model="lastName"
                                        type="text"
                                        pattern="[A-Za-z]{0,20}"
                                        :keyup.enter="signupRequest"
                                   />
                              </div>
                         </div>

                         <svg id="line">
                              <line class="cls" x1="1.2" y1="0" x2="1.2" y2="1000" />
                         </svg>

                         <div class="fields">
                              <label>Username</label>
                              <div class="formfield">
                                   <img id="signup_username_icon" src="../assets/profile2.svg" />
                                   <input
                                        v-model="username"
                                        id="signup_username"
                                        type="text"
                                        required
                                        title="Must be at least 6 characters long."
                                        pattern="[a-zA-Z0-9_-]{6,20}"
                                        :keyup.enter="signupRequest"
                                   />
                                   <span>Your username must be at least 6 characters long.</span>
                              </div>

                              <label>Password:</label>
                              <div class="formfield">
                                   <img id="signup_password_icon" src="../assets/lock.svg" />
                                   <input
                                        v-model="password"
                                        id="signup_password"
                                        name="password"
                                        type="password"
                                        required
                                        title="Must contain 1 lowercase, 1 uppercase and a number"
                                        :keyup.enter="signupRequest"
                                   />
                                   <span>Your password must be at least 10 characters long.</span>
                              </div>

                              <label>Confirm Password:</label>
                              <div class="formfield">
                                   <img id="signup_confirmpassword_icon" src="../assets/lock.svg" />
                                   <input
                                        id="signup_confirmPassword"
                                        type="password"
                                        :keyup.enter="signupRequest"
                                        v-model="confirmPassword"
                                   />
                              </div>
                         </div>
                    </div>
                    <div id="signup_typeContainer">
                         <label>Are you a landlord?</label>
                         <input id="signup_type" v-model="landlord" type="checkbox" />
                    </div>
                    <span id="signup_error"></span>
                    <input
                         id="signup_submit"
                         type="submit"
                         value="Agree and sign up"
                         v-on:click.prevent="signupRequest()"
                    />
                    <div
                         id="tos"
                    >By selecting Agree and signup above, I agree to OffCampus’s Terms of Service, Payments Terms of Service, Privacy Policy, and Nondiscrimination Policy.</div>
               </div>
          </div>
     </div>
</template>

<script>
var jwtDecode = require("jwt-decode");
import axios from "axios";
import VueJwtDecode from "vue-jwt-decode";
export default {
     name: "Signup",
     data() {
          return {
               email: "",
               firstName: "",
               lastName: "",
               username: "",
               password: "",
               confirmPassword: "",
               landlord:"false"
          };
     },
     methods: {
          valid() {
               if (
                    this.email == "" ||
                    !$("#signup_email").prop("validity").valid
               ) {
                    $("#signup_error").text(
                         "Error: please enter a valid email."
                    );
               } else if (
                    this.firstName == "" ||
                    !$("#signup_firstName").prop("validity").valid
               ) {
                    $("#signup_error").text(
                         "Error: please enter a valid first name."
                    );
               } else if (
                    this.lastName == "" ||
                    !$("#signup_lastName").prop("validity").valid
               ) {
                    $("#signup_error").text(
                         "Error: please enter a valid last name."
                    );
               } else if (
                    this.username == "" ||
                    !$("#signup_username").prop("validity").valid
               ) {
                    $("#signup_error").text(
                         "Error: please enter a valid username."
                    );
               } else if (this.password.length < 10) {
                    $("#signup_error").text(
                         "Error: passwords must be at least 10 characters long."
                    );
               } else if (this.password != this.confirmPassword) {
                    $("#signup_error").text("Error: passwords must match.");
               } else {
                    return true;
               }
          },
          signupRequest() {
               $("#signup_error").text("");
               if (this.valid()) {
                    $("#signup_submit").attr("disabled", "disabled");
                    $("#signup_error").text("...");
                    var form = $("#signup_form");
                    var type = "USERLEASERACCOUNT";
                    if(this.landlord == true) type = "USERTENANTACCOUNT";
                    var formData = {
                         email: this.email,
                         username: this.username,
                         accountType: type,
                         firstName: this.firstName,
                         lastName: this.lastName,
                         bio: "",
                         imageUrl: "",
                         password: this.password
                    };
                    axios({
                         method: "post",
                         url: this.$store.get("apiUrl") + "users/register",
                         data: formData,
                         headers: {
                              withCredentials: true
                         }
                    })
                         .then(response => {
                              this.$store.set("token", response.data.encoded);
                              this.$store.set(
                                   "username",
                                   response.data.username
                              );
                              var decoded = VueJwtDecode.decode(
                                   response.data.encoded
                              );
                              this.$store.set("email", decoded.data);
                              this.$router.push("home/");
                         })
                         .catch(function(error) {
                              $("#signup_submit").removeAttr("disabled");
                              $("#signup_error").text(error);
                         });
               }
          }
     },
     computed: {},
     components: {},
     mounted() {}
};
</script>

